
.medicationForm {
	text-align:left;
	padding:0 5px;
}

.dropdownFields {
	margin: 0 20px 0 10px;
}

.reminderFormTitle {
	text-align:center;
}

.addMedsPanel {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	background-color: #4d6684;
    border:0;
    color:white;
    border-radius:10px;
}

.wellDay {
	background-color: #4d6684;
	color: #fff;
	border-radius: 10px;
	font-size:15px;
	margin-bottom:0;
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.remindersText {
	text-align:left;
}

.Input {
	margin-left:10px;
}

.dateLabel {
	margin-left:10px;
}

.medicationForm {
	text-align:left;
}

.hide {
	display: none;
}

.deleteBtn {
	margin-left: 10px;
	position: absolute;
	right: 0;
	top: 10px;
	margin-right: 15px;
	padding: 5px;
	font-size: 9px;
	color: rgb(181, 92, 111);
	font-weight: bold;
	margin-bottom: 0;
}

.reminders {
	text-align: left;
	padding: 10px 0 10px 20px;
	position: relative;
	background-color:#cde6f5;
	border-radius:10px;
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}


.reminderTitle {
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.5;
	padding-right: 15px;
}

.panelTitle{
	padding-bottom:10px;
}

.currentReminders{
	margin-bottom:20px;
}

.panelLabel{
	font-size:17px;
	font-weight:100;
}

.addReminderBtn{
	background-color:#cde6f5;
	float:right;
}




